/*
 * local css
 */


/* Image Fit */
.card-img-top.fit {
  height: 160px;
  object-fit: cover;
}

/* Doctor Cards */
.avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
}

.doctor-card {
  border: 1px solid #eaeaea;
  border-radius: 14px;
  padding: 20px;
  text-align: center;
  transition: transform 0.18s, box-shadow 0.18s;
  height: 100%;
}

.doctor-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.08);
}

/* Specialty Cards */
.specialty-card {
  border: 1px solid #eaeaea;
  border-radius: 14px;
  overflow: hidden;
  height: 100%;
}

